  /* Toggle Switch - Checkbox styling 
https://www.w3schools.com/howto/howto_css_switch.asp */
/* The switch - the box around the slider */
.VolumeControl_switch__14pnI {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .VolumeControl_switch__14pnI input {
    opacity: 0;
    width: 0;
    height: 0;
  }


  /* The slider */
.VolumeControl_slider__14_0H {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .VolumeControl_slider__14_0H:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .VolumeControl_slider__14_0H {
    background-color: #2196F3;
  }
  
  input:focus + .VolumeControl_slider__14_0H {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .VolumeControl_slider__14_0H:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
